import { getHideBaseStyleMixin } from '../mixins/act/hide-base-style';
import PressActCheckbox from './press-act-checkbox.vue';
import { getActClass } from './utils';


export default {
  name: 'PressActInputPhoneDialog',
  options: {
    styleIsolation: 'shared',
  },
  components: {
    PressActCheckbox,
  },
  mixins: [getHideBaseStyleMixin()],
  props: {
    show: {
      type: Boolean,
      desc: '是否展示弹窗',
      default: false,
      required: false,
    },
    phone: {
      type: String,
      desc: '手机号码',
      default: '',
      required: false,
    },
    needPhoneCheck: {
      type: Boolean,
      desc: '是否需要验证手机号合法性',
      default: false,
      required: false,
    },
    code: {
      type: String,
      desc: '手机号验证码',
      default: '',
      required: false,
    },
    getCodeText: {
      type: String,
      desc: '获取验证码按钮文案',
      default: '',
      required: false,
    },
    isDisableGetCode: {
      type: Boolean,
      desc: '获取验证码按钮是否可点击',
      default: false,
      required: false,
    },
    remark: {
      type: String,
      desc: '兑换说明',
      default: '请填写手机号，将兑换到对应账号',
      required: false,
    },
    title: {
      type: String,
      desc: '弹窗标题',
      default: '填写手机号码',
    },
    protocolName: {
      type: String,
      desc: '协议名称',
      default: null,
    },
    userProtocolName: {
      type: String,
      desc: '用户协议名称',
      default: null,
    },
    isAgreeProtocol: {
      type: Boolean,
      default: false,
      required: false,
      desc: '是否选中协议',
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
    hideTipStyle: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    closeDialog() {
      this.$emit('clickCloseButton');
      this.$emit('update:show', false);
    },
    onClickButton() {
      this.$emit('clickButton');
    },
    onClickProtocol() {
      this.$emit('clickProtocol');
    },
    onClickUserProtocol() {
      this.$emit('clickUserProtocol');
    },
    onClickGetCode() {
      this.$emit('clickGetCodeButton');
    },
    changeIsAgreeProtocol(checked) {
      this.$emit('update:isAgreeProtocol', checked);
    },
    changePhone(e) {
      this.$emit('update:phone', e.target.value);
    },
    changeCode(e) {
      this.$emit('update:code', e.target.value);
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};
