import { getActClass } from './utils';


export default {
  name: 'PressActCheckbox',
  options: {
    styleIsolation: 'shared',
  },
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
    customClass: {
      type: String,
      default: '',
    },
  },
  emits: [
    'input',
  ],
  data() {
    return {};
  },
  methods: {
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};
